<template>
    <div id="app" @touchstart.stop="handleTap">
        <transition name="slide">
            <router-view />
        </transition>
    </div>
</template>

<script>
export default {
    mounted() {
        this.$store.dispatch("getUser");
    },
    methods: {
        //防止移动端hover事件触摸后不隐藏
        handleTap(e) {
            e.target.dataset.type === "header-img"
                ? this.$store.commit("HIDENAV", false)
                : this.$store.commit("HIDENAV", true);
        }
    }
};
</script>

<style lang="scss">
#app {
    font-family: "Microsoft YaHei", Arial;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: $uni-text-color;
    position: relative;
    width: 100%;
    height: 100%;
    @extend .transiton-slide; //路由动画
    @extend .transiton-fade; //路由动画
    @extend .transiton-nav-show; //nav
    @media screen and(max-width: 500px) {
        width: 100vw;
        height: 100vh;
        overflow: hidden;
    }
}

body {
    position: relative;
    width: 100%;
    height: 100%;
}
</style>
