import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home'
import HomeIndex from '@/views/HomeIndex'



Vue.use(VueRouter)


const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    name: 'add-blog',
    path: '/add-blog',
    component: () => import('@/views/BlogAdd'),
  },
  {
    name: 'show-blog',
    path: '/show-blog/:id',
    component: () => import('@/views/BlogShow'),
  },
  {
    name: 'register',
    path: '/register',
    component: () => import('@/views/Register'),
  },
  {
    name: 'login',
    path: '/login',
    component: () => import('@/views/Login'),
  },
  {
    path: '/my-page',
    component: () => import('@/views/MyPage'),
    children: [
      {
        name: 'my-page',
        path: '',
        component: () => import('@/views/MyPageIndex'),
      },
      {
        name: 'my-page/search',
        path: '/my-page/search',
        component: () => import('@/views/MyPageUserSeearch'),
      }
    ]
  },
  {
    path: "*",
    component: () => import('@/views/NotFound')
  }
]

const headerNav = [
  {
    path: '/download',
    component: () => import('@/views/Download'),
    value: '下载',
    name: 'download'
  },
  {
    path: '/home',
    component: Home,
    value: '首页',
    children: [
      {
        path: '',
        name: 'home',
        component: HomeIndex,
        meta: {
          keepAlive: true
        }
      },
      {
        name: 'search',
        path: '/home/search',
        component: () => import('@/views/HomeSearch'),
      }

    ]
  }
]







const router = new VueRouter({
  routes: [...routes, ...headerNav]
})



router.beforeEach((to, from, next) => {
  let isLogin = localStorage.getItem("BLOG_TOKEN") ? true : false;
  if (to.path === "/login" || to.path === '/register' || to.name === "show-blog") {
    next()
  } else if ((to.path === '/my-page' || to.path === '/edit') && !isLogin) {
    next("/login")
  } else {
    next()
  }
})

export default router;

export { headerNav }