import http from '@/ajax'

const blog = {
    state: {
        indexBlogs: [],
        myBlogs: [],
        myLikedBlogs: [],
        indexTypeNavLists: [],
        allTypeLists: [],
        //轮播图内容
        swiperList: [
            {
                img_url: "https://cn.vuejs.org/images/logo.png",
                path: "https://cn.vuejs.org/",
                tips: "vue2.x官网",
                _id: "5ea6bfda1163ba444081483d",
            }, {
                img_url: "https://cn.vuejs.org/images/logo.png",
                path: "https://vuex.vuejs.org/zh/",
                tips: "vuex",
                _id: "5ea6bfda1163ba444081483e",
            }
        ],
        homeSearchList: []
    },
    mutations: {
        ['SETYPENAVLIST'](state, list) {
            // 点击后保存在本地
            localStorage.setItem(
                "BLOG_INDEX_NAV_TYPE_LIST",
                JSON.stringify(list)
            );
            state.indexTypeNavLists = list
        },
        ['SETBLOGS'](state, blogs) {
            // state.indexBlogs = state.indexBlogs.concat(blogs)
            state.indexBlogs = blogs
        },
        ['SETMYBLOGS'](state, blogs) {
            // state.myBlogs = state.myBlogs.concat(blogs)
            state.myBlogs = blogs
        },
        ['SETMYLIKEDBLOGS'](state, blogs) {
            // state.myBlogs = state.myBlogs.concat(blogs)
            state.myLikedBlogs = blogs
        },
        ["RESETUSER"](state) {
            state.myBlogs = []
            state.myLikedBlogs = []
        },
        ["TOGGLELIKES"](state, { blogID, userID }) {
            state.myLikedBlogs.forEach((v, i) => {
                if (v._id === blogID) {
                    state.myLikedBlogs.splice(i, 1)
                }
            })
        },
        ["DELETEMYBLOG"](state, id) {
            state.myBlogs.forEach((v, i) => {
                if (v._id === id) {
                    state.myBlogs.splice(i, 1)
                }
            })
        }
    },
    actions: {
        async  blogAddIMG({ state }, formData) {
            let data = await http({
                url: '/file-service/upload?type=blogIMG',
                data: formData,
                type: "POST",
                headers: { 'Content-Type': 'multipart/form-data' }
            })

            return data
        }
        ,
        async homeSearch({ state }, keyword) {
            let url = "/blog-service/search"
            let data = await http({ url, data: { keyword } });
            state.homeSearchList = data
            return data
        },
        // 获取nav列表信息
        async getLeftNavList({ commit, dispatch }, force) {
            let data = null;
            let typeLIst = localStorage.getItem("BLOG_INDEX_NAV_TYPE_LIST");
            if (!force && typeLIst) {
                data = JSON.parse(typeLIst);
                let lastSaveItem = data.filter(v => v.isActive === true);
                if (lastSaveItem.length) {
                    dispatch("getIndexPageBlog", {
                        name: lastSaveItem[0].name
                    });
                } else {
                    dispatch('getLeftNavList', true)
                }
            } else {
                let url = "/blog-service/get-types/list"
                data = await http({ url });

                data = data.map((v, i) => {
                    if (i === 0) {
                        dispatch('getIndexPageBlog', { name: v.name })
                        return { ...v, isActive: true, title: v.name };
                    } else {
                        return { ...v, isActive: false, title: v.name };
                    }
                });
            }

            commit("SETYPENAVLIST", data);
        },
        // 设置nav列表信息, 同时获取对应的blog list
        async setLeftNavList({ commit, state, dispatch }, key) {
            state.indexTypeNavLists.forEach(v => {
                if (v.name === key) {
                    if (v.isActive === true) return;
                    v.isActive = true;
                    dispatch("getIndexPageBlog", { name: key });
                } else {
                    v.isActive = false;
                }
            })

            commit("SETYPENAVLIST", state.indexTypeNavLists);
        },
        async getBlogTypes({ state }) {
            let url = "/blog-service/get-types"
            let types = await http({ url });
            state.allTypeLists = types
            return types
        },
        async deleteBlog({ commit }, id) {
            let url = "/blog-service/delete/" + id
            return await http({ url });
        },

        async getMyliked({ commit }) {
            let url = "/blog-service/get-my-liked"
            let blogs = await http({ url });
            commit("SETMYLIKEDBLOGS", blogs)
            return blogs
        },


        async toggleLike({ commit }, { blogID, userID }) {
            let url = "/blog-service/toggle-likes"
            let data = await http({ url, data: { id: blogID } });
            if (data) {
                commit("TOGGLELIKES", { blogID, userID })
                return data
            }
        },

        async AddBlog({ state }, { context, tags }) {
            let data = await http({
                url: "/blog-service/add-blog",
                type: "POST",
                data: {
                    context,
                    tags
                }
            })
            return data
        },

        async editBlog({ state }, { context, tags }) {
            let data = await http({
                url: "/blog-service/edit-blog",
                type: "POST",
                data: {
                    context,
                    tags
                }
            })
            return data
        },

        async getIndexPageBlog({ state, commit }, { name }) {

            let url = "/blog-service/get-index-blog"
            let data = await http({ url, data: { page: 0, type: name } });
            let { blogs, specialInfo } = data;
            (specialInfo ? state.swiperList = specialInfo.swipers : "");
            commit("SETBLOGS", blogs);
        },

        async getMyBlog({ commit, state }, type) {

            // if (type === 'REFRESHMYBLOGS') {
            state.myBlogs = []
            // }

            let url = "/blog-service/get-my-blog"
            let blogs = await http({ url, data: { page: 0 } });

            commit("SETMYBLOGS", blogs);

        },

        async getBlogById({ state }, id) {
            let url = "/blog-service/get-index-blog"
            return await http({ url, data: { id } });
        },

        async searchBlogByKeyWord({ state }, id) {

        },
    }
}



export default blog