<template>
    <div class="home-header" @click.stop="onclick">
        <div class="header-inner">
            <ul class="header-nav">
                <li class="sm-nav-container" v-show="type">
                    <span
                        data-switch="me"
                        class="iconfont icon-xuanxiangqiacaidananniu"
                    ></span>
                </li>
                <li class="header-img">
                    <img
                        class="header-img"
                        src="../assets/csdn-logo.png"
                        alt="logo"
                    />
                </li>
                <li
                    class="nav-item"
                    v-for="(item, index) in headerNav"
                    :key="index"
                >
                    <router-link class="link-item" :to="item.path">{{
                        item.value
                    }}</router-link>
                </li>
                <div class="header-search" v-if="!hideSearch">
                    <HomeSearchTool
                        :placeholder="placeholder"
                        @handleSearch="handleSearch"
                    />
                </div>
            </ul>
            <div class="header-user">
                <ul class="hearder-user-nav">
                    <li class="user-nav-item">
                        <p class="create-link" to="/">
                            <span class="iconfont icon-xieboke">创作中心</span>
                        </p>
                        <div
                            :class="[
                                'nav-content',
                                'create',
                                { 'hide-mixin': hideAllNav }
                            ]"
                        >
                            <dd
                                v-for="(item, index) in createNavList"
                                :key="index"
                            >
                                <router-link :to="item.link">{{
                                    item.value
                                }}</router-link>
                            </dd>
                        </div>
                    </li>
                    <li class="header-tips">
                        <p class="tips-link">
                            <span
                                class="iconfont icon-tips"
                                data-type="header-img"
                            ></span>
                        </p>
                        <div
                            :class="[
                                'nav-content',
                                'tips',
                                { 'hide-mixin': hideAllNav }
                            ]"
                        >
                            <dd
                                v-for="(item, index) in tipsNavList"
                                :key="index"
                            >
                                <router-link :to="item.link">{{
                                    item.value
                                }}</router-link>
                            </dd>
                        </div>
                    </li>
                    <li>
                        <p class="user-link">
                            <img
                                data-type="header-img"
                                :src="
                                    user.avatar || '/public/images/default.png'
                                "
                            />
                        </p>
                        <div
                            :class="[
                                'nav-content',
                                'user',
                                { 'hide-mixin': hideAllNav }
                            ]"
                        >
                            <dd
                                v-for="(item, index) in userNavList"
                                :key="index"
                            >
                                <router-link :to="item.link">{{
                                    item.value
                                }}</router-link>
                            </dd>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
        <transition name="nav">
            <SMNav v-show="showNav" :navList="NavLists" :onclick="onclick" />
        </transition>
    </div>
</template>

<script>
export default {
    props: {
        NavLists: Array /*移动端的nav.为了页面的差异性,使用参数的方式*/,
        type: String,
        searchPage: String,
        placeholder: String
    },
    inheritAttrs: false,
    data() {
        return {
            hideSearch: false,
            createNavList: [
                {
                    link: "/add-blog",
                    value: "写博客"
                }
            ],
            tipsNavList: [
                {
                    link: "/download",
                    value: "评论"
                },
                {
                    link: "/download",
                    value: "关注"
                },
                {
                    link: "/download",
                    value: "点赞"
                }
            ],
            userNavList: [
                {
                    link: "/my-page?key=我的关注",
                    value: "我的关注"
                },
                {
                    link: "/my-page?key=我的收藏",
                    value: "我的收藏"
                },
                {
                    link: "/my-page?key=个人中心",
                    value: "个人中心"
                },
                {
                    link: "/my-page?key=我的博客",
                    value: "我的博客"
                },
                {
                    link: "/login",
                    value: "退出登录"
                }
            ],
            showNav: false
        };
    },
    mounted() {
        this.checkShow();
    },
    methods: {
        handleSearch(v) {
            switch (this.searchPage) {
                case "home":
                    this.$router.push("/home/search?key=" + v);
                    break;
                case "my-page":
                    this.$router.push("/my-page/search?key=" + v);
                    break;
                case "download":
                    console.log("download search");
                    break;
                case "show-blog":
                    console.log("show-blog search");
                    break;
                default:
                    this.$message({
                        type: "error",
                        showClose: true,
                        message: "未定义页面搜索事件方法"
                    });
                    break;
            }
        },
        checkShow() {
            console.log(this.searchPage);
            this.searchPage === "show-blog"
                ? (this.hideSearch = true)
                : (this.hideSearch = false);
        },
        onclick(e) {
            let me = e.target.dataset.switch;
            if (me === "me") {
                this.showNav = !this.showNav;
            } else {
                this.showNav ? (this.showNav = false) : "";
            }
        }
    }
};
</script>

<style lang="scss" scoped>
.home-header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 3;
    box-sizing: border-box;
    height: 44px;
    padding: 0 $uni-spacing-col-base;
    background: #fff;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05);

    .header-inner {
        width: 100%;
        max-width: 1200px;
        height: 44px;
        margin: 0 auto;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;

        .header-nav {
            width: 100%;
            .router-link-active {
                border-bottom: 2px solid #e33e33;
                color: #222226;
            }
            height: 44px;
            display: flex;
            flex-direction: row;
            font-size: 14px;
            .header-img {
                width: 88px;
                box-sizing: border-box;
                height: 44px;
                padding-right: 8px;
                .header-img {
                    width: 88px;
                    height: 44px;
                }
            }
            .nav-item {
                @extend .hover;
                white-space: nowrap;
                height: 44px;
                box-sizing: border-box;
                padding: 0 8px;
                a {
                    display: block;
                    height: 100%;
                    width: 100%;
                }
            }
            li {
                line-height: 44px;
            }

            .sm-nav-container {
                display: none;
                .iconfont {
                    padding: 0 1.5rem;
                    background-color: $uni-bg-color-grey;
                    height: 100%;
                    display: block;
                    color: $uni-color-error;
                }
            }
        }

        .header-search {
            width: 100%;
            max-width: 350px;
            padding-left: 16px;
            height: 44px;
        }

        .header-user {
            height: 44px;
            .hearder-user-nav {
                display: flex;
                flex-direction: row;
                img {
                    width: 28px;
                    height: 28px;
                    margin-top: 7px;
                    border-radius: $uni-border-radius-circle;
                }
                & > li {
                    box-sizing: border-box;
                    padding: 0 8px;
                    position: relative;
                    &:hover {
                        .nav-content {
                            display: block;
                        }
                    }
                    a {
                        display: block;
                        height: 44px;
                        line-height: 44px;
                    }
                    .create-link {
                        background-color: $uni-color-error;
                        border-radius: 5px;
                        width: 94px;
                        box-sizing: border-box;
                        line-height: 32px;
                        color: #fff;
                        padding: 0 5px;
                        margin-top: 6px;
                        height: 32px;
                        span {
                            font-size: 14px;
                            &::before {
                                padding-right: 3px;
                            }
                        }
                    }
                    .tips-link {
                        line-height: 44px;
                        span {
                            font-size: 1.5rem;
                        }
                    }
                    .hide-mixin {
                        @media screen and (max-width: 500px) {
                            display: none !important;
                        }
                    }
                    .nav-content {
                        display: none;
                        overflow: hidden;
                        position: absolute;
                        background-color: #fff;
                        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);
                        dd {
                            width: 100%;
                            height: 40px;
                            text-align: center;
                            font-size: 14px;
                            @extend .hover;
                        }
                    }
                    .create {
                        width: 94px;
                        box-sizing: border-box;
                    }
                    .tips {
                        min-width: 120px;
                        box-sizing: border-box;
                        transform: translateX(-48px);
                    }
                    .user {
                        width: 135px;
                        transform: translateX(-100px);
                        box-sizing: border-box;
                    }
                }
            }
        }
        @media screen and(max-width: 500px) {
            .header-nav {
                font-weight: bold;
                color: $uni-text-color;
                .header-img,
                .header-tips,
                .header-search {
                    display: none;
                }

                .sm-nav-container {
                    display: block;
                }
            }
            .header-user {
                .hearder-user-nav {
                    .user-nav-item {
                        display: none;
                    }
                }
            }
        }
    }

    ol,
    ul {
        list-style: none;
    }
}
</style>